import {Heading, Wrap, Box, Center, SimpleGrid, useMediaQuery} from '@chakra-ui/react'
import { useAnimation, motion } from "framer-motion";
import { useInView } from 'react-intersection-observer';
import { useEffect, useRef } from 'react';
import ProjectCard from './ProjectCard';
import detections from '../images/detections.gif'
import immibot from '../images/immibot.png'
import schedio from "../images/schedio.jpg"
import mahjong from "../images/mahjong.gif"
import saas from "../images/saas.png"
import legback from "../images/legback.png"
import sourceacademy from "../images/sourceacademy.jpeg"

function Projects() {
    const projects = [
        {
            'title': 'Canteen Tracker',
            'desc': 'Computer vision model developed to measure crowd density and adherence to social distancing. Built using YOLOv3.',
            'image': detections,
            'website': null,
            'github': "https://github.com/JovenSoh/School-YOLO",
        },
        {
            'title': 'Source Academy',
            'desc': 'Opensource learning platform for NUS CS students.',
            'image': sourceacademy,
            'website': null,
            'github': 'https://github.com/JovenSoh/Signal-Modulation-Detection-using-Conv1D',
        },
        {
            'title': 'Legback',
            'desc': 'Legal compliance platform for SMEs.',
            'image': legback,
            'website': null,
            'github': "https://www.legback.com/",
        },
        {
            'title': 'Immibot',
            'desc': 'AI-powered immigration consultant.',
            'image': immibot,
            'website': "https://immibot.ai/",
            'github': null,
        },
        {
            'title': 'SG Mahjong',
            'desc': 'Play Singapore Mahjong with your friends! Built using React and Websockets.',
            'image': mahjong,
            'website': "https://jovensoh.github.io/sgmahjong",
            'github': null,
        },
        {
            'title': 'Schedio+Partners',
            'desc': 'Architect of the Schedio+Partners website.',
            'image': schedio,
            'website': "https://schediopartners.com/",
            'github': null,
        },
        {
            'title': 'SaaSVentures',
            'desc': 'Web3 business that I am a part of.',
            'image': saas,
            'website': "https://saasventures.xyz/",
            'github': null,
        }
    ];

    const controls = useAnimation();
    const [ref, inView] = useInView();
    const hasAnimated = useRef(false);
    const [isMobile] = useMediaQuery("(max-width: 768px)");

    useEffect(() => {
        if (inView && !hasAnimated.current) {
            controls.start("visible");
            hasAnimated.current = true;
        }
    }, [controls, inView]);

    const cardVariants = {
        hidden: {
            opacity: "0%",
        },
        visible: (custom) => ({
            opacity: "100%",
            transition: { delay: custom, ease: [0.455, 0.03, 0.515, 0.955], duration: 1.5 }
        })
    };

    return (
        <>
            <Heading mt='5vh' align='center' >Past Work</Heading>
            <Center>
                <Wrap className="hexagon-wrapper" w={isMobile ? '430px' : '600px'} justifyContent='center' alignItems='center' minH='80vh' mb='15vh' ref={ref}>
                    <SimpleGrid columns={isMobile ? 2 : { base: 2, md: 2 }} spacing={1} pl={isMobile ? '50px' : '75px'}>
                        {projects.slice(0, 2).map((project, index) => (
                            <Box as={motion.div} animate={controls} initial='hidden' variants={cardVariants} key={index} mb={isMobile ? '-36px' : '-55px'}>
                                <ProjectCard props={project} index={index} />
                            </Box>
                        ))}
                    </SimpleGrid>
                    <SimpleGrid columns={isMobile ? 3 : { base: 1, md: 3 }} spacing={1}>
                        {projects.slice(2, 5).map((project, index) => (
                            <Box as={motion.div} animate={controls} initial='hidden' variants={cardVariants} key={index + 2} mb={isMobile ? '-36px' : '-55px'}>
                                <ProjectCard props={project} index={index + 2} />
                            </Box>
                        ))}
                    </SimpleGrid>
                    <SimpleGrid columns={isMobile ? 2 : { base: 2, md: 2 }} spacing={1} pl={isMobile ? '50px' : '75px'}>
                        {projects.slice(5, 7).map((project, index) => (
                            <Box as={motion.div} animate={controls} initial='hidden' variants={cardVariants} key={index + 5}>
                                <ProjectCard props={project} index={index + 5} />
                            </Box>
                        ))}
                    </SimpleGrid>
                </Wrap>
            </Center>
        </>
    );
}

export default Projects;