import { Wrap, Box, Heading, Text } from '@chakra-ui/react';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import TextSlide from './TextSlide';
import { useState, useEffect, useRef } from 'react';
import CategorizedSkills from './CategorizedSkills';

const skillScores = {
  "JavaScript": { AI: 0, "Frontend Development": 15, "Backend Development": 15 },
  "Python": { AI: 15, "Frontend Development": 9, "Backend Development": 9 },
  "C++": { AI: 9, "Frontend Development": 0, "Backend Development": 9 },
  "HTML": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "CSS": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "React": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "NodeJS": { AI: 0, "Frontend Development": 9, "Backend Development": 15 },
  "Tensorflow": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Seaborn": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Matplotlib": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "MongoDB": { AI: 0, "Frontend Development": 0, "Backend Development": 15 },
  "SQL": { AI: 0, "Frontend Development": 0, "Backend Development": 15 },
  "Octave": { AI: 9, "Frontend Development": 0, "Backend Development": 0 },
  "Stata": { AI: 9, "Frontend Development": 0, "Backend Development": 0 },
  "GAN": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "NLP": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Java": { AI: 0, "Frontend Development": 0, "Backend Development": 9 },
  "Computer Vision": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Pandas": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Numpy": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "MaterialUI": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "ChakraUI": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "WebSockets": { AI: 0, "Frontend Development": 9, "Backend Development": 15 },
  "OpenCV": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "Next.js": { AI: 0, "Frontend Development": 15, "Backend Development": 9 },
  "Figma": { AI: 0, "Frontend Development": 15, "Backend Development": 0 },
  "Nginx": { AI: 0, "Frontend Development": 0, "Backend Development": 15 },
  "Langchain": { AI: 15, "Frontend Development": 0, "Backend Development": 0 },
  "GPT": { AI: 15, "Frontend Development": 0, "Backend Development": 0 }
};

function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return { width, height };
}

function useWindowDimensions() {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

  useEffect(() => {
    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return windowDimensions;
}

function Skills() {
  const { height, width } = useWindowDimensions();
  const controls = useAnimation();
  const [ref, inView] = useInView();
  const hasAnimated = useRef(false);
  const [scores, setScores] = useState({ AI: 0, "Frontend Development": 0, "Backend Development": 0 });

  useEffect(() => {
    if (inView && !hasAnimated.current) {
      controls.start("visible");
      hasAnimated.current = true;
    }
  }, [controls, inView]);

  const handleSkillToggle = (skill, isSelected) => {
    const skillScore = skillScores[skill];
    setScores((prevScores) => ({
      AI: prevScores.AI + (isSelected ? skillScore.AI : -skillScore.AI),
      "Frontend Development": prevScores["Frontend Development"] + (isSelected ? skillScore["Frontend Development"] : -skillScore["Frontend Development"]),
      "Backend Development": prevScores["Backend Development"] + (isSelected ? skillScore["Backend Development"] : -skillScore["Backend Development"])
    }));
  };

  const letterVariants = {
    hidden: { y: "120%" },
    visible: (custom) => ({
      y: 0,
      transition: { delay: custom, ease: [0.455, 0.03, 0.515, 0.955], duration: 1 }
    })
  };

  const draw = {
    hidden: { strokeDashoffset: 100 },
    visible: {
      strokeDashoffset: 0,
      transition: { strokeDashoffset: { duration: 1.5 } }
    }
  };

  const Line = ({ percentage }) => {
    const controls = useAnimation();
    const [ref, inView] = useInView();
    const hasAnimated = useRef(false);

    useEffect(() => {
      if (inView && !hasAnimated.current) {
        controls.start("visible");
        hasAnimated.current = true;
      }
    }, [controls, inView]);

    return (
      <motion.svg width="100%" height="10" viewBox="0 0 100% 10" initial="hidden" animate={controls} ref={ref}>
        <motion.line x1="0" y1="5" x2="100%" y2="5" stroke="#FFF" pathLength="100" strokeDasharray="100" />
        <motion.line x1="0" y1="5" x2={`${percentage}%`} y2="5" stroke="#30D1FA" pathLength="100" strokeDasharray="100" variants={draw} />
      </motion.svg>
    );
  };

  const getWidth = () => (width < 1200 ? 600 : 1200);

  let paragraph = "Over the course of my studies, I have picked up various programming languages while working on projects involving artificial intelligence and fullstack development. I can help you create responsive websites that capture the attention of your audience and generate leads.";

  const containerRef = useRef(null);
  const containerHeight = ref.offsetHeight;
  const windowHeight = window.innerHeight;
  const heightA = containerHeight > windowHeight ? containerHeight : windowHeight;

  return (
    <div ref={containerRef}>
      <Wrap w='full' h={heightA} justify='center' overflow-y='auto'>
        <Box w='600px' px='30px' overflow='auto' bg='rgba(18,18,18,0.9)' maxH='50vh' ref={ref}>
          <Heading>Skills</Heading>
          <TextSlide text={paragraph} controls={controls} delay={0.5} />
        </Box>
        <Box w='600px' overflow='hidden' px='12px' bg='rgba(18,18,18,0.9)' maxH='60vh'>
          <Box as={motion.div} animate={controls} initial='hidden' variants={letterVariants} custom={1}>
            <CategorizedSkills onSkillToggle={handleSkillToggle} />
          </Box>
        </Box>
        <Box px='30px' py='15px' overflow='hidden' w={getWidth()} bg='rgba(18,18,18,0.9)'>
          <Box as={motion.div} animate={controls} initial='hidden' variants={letterVariants} custom={1.5}>
            <Text my='5px'>Artificial Intelligence</Text>
            <Line percentage={scores.AI} />
            <Text my='5px'>Frontend Development</Text>
            <Line percentage={scores["Frontend Development"]} />
            <Text my='5px'>Backend Development</Text>
            <Line percentage={scores["Backend Development"]} />
          </Box>
        </Box>
      </Wrap>
    </div>
  );
}

export default Skills;